let common={};
/*
*htmlfont-size
* */
window.onload = function(){
  let uiWidth = 750;
  document.documentElement.style.fontSize =
      document.documentElement.clientWidth * 100/uiWidth  + 'px';
};
global.setTitle = function (str) {
    document.getElementById('titleId').innerText = str;
};
global.setUserInfo = function (user) {
    sessionStorage.setItem('KEY_USER',JSON.stringify(user));
};
global.getUserInfo = function () {
    return JSON.parse(sessionStorage.getItem('KEY_USER'));
};
/*  */
global.getPreMonth = function(date) {
    var arr = date.split('-');
    var year = arr[0]; //
    var month = arr[1]; //
    var day = arr[2]; //
    var days = new Date(year, month, 0);
    days = days.getDate(); //
    var year2 = year;
    var month2 = parseInt(month) - 1;
    if (month2 == 0) {
        year2 = parseInt(year2) - 1;
        month2 = 12;
    }
    var day2 = day;
    var days2 = new Date(year2, month2, 0);
    days2 = days2.getDate();
    if (day2 > days2) {
        day2 = days2;
    }
    if (month2 < 10) {
        month2 = '0' + month2;
    }
    var t2 = year2 + '/' + month2 + '/' + day2;
    return t2;
};
/**
 * 
 * @date yyyy-mm-dd2014-01-25
 */
global.getNextMonth = function(date) {
    var arr = date.split('-');
    var year = arr[0]; //
    var month = arr[1]; //
    var day = arr[2]; //
    var days = new Date(year, month, 0);
    days = days.getDate(); //
    var year2 = year;
    var month2 = parseInt(month) + 1;
    if (month2 == 13) {
        year2 = parseInt(year2) + 1;
        month2 = 1;
    }
    var day2 = day;
    var days2 = new Date(year2, month2, 0);
    days2 = days2.getDate();
    if (day2 > days2) {
        day2 = days2;
    }
    if (month2 < 10) {
        month2 = '0' + month2;
    }

    var t2 = year2 + '-' + month2 + '-' + day2;
    return t2;
}
Date.prototype.Format2String = function (fmt) {
    if(!this) return null;
    var o = {
        "M+": this.getMonth() + 1, //
        "d+": this.getDate(), //
        "h+": this.getHours(), //
        "m+": this.getMinutes(), //
        "s+": this.getSeconds(), //
        "q+": Math.floor((this.getMonth() + 3) / 3), //
        "S": this.getMilliseconds() //
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
        if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
};
/**
 * 
 * @returns {number}
 */
Date.prototype.getMonthDay = function () {
    let curDate = this;
    let curMonth = curDate.getMonth();
    curDate.setMonth(curMonth + 1);
    curDate.setDate(0);
    return curDate.getDate();
};
/***
 * 
 * @param fmt yyyy()MM()dd()hh()mm()ss()S()q()
 * @returns {String}  fmt
 * @constructor
 */
String.prototype.Format2String = function (fmt) {
    if(this == '') return null;
    var reg = new RegExp("^[0-9]*$");
    if(reg.test(this)){ // 
        return new Date(this).Format2String(fmt);
    }
    var result = new Date(this.replace(/-/g,"/"));
    if(result.toString()=='Invalid Date'){
        console.log('yyyy-MM-dd hh:MM:ss  yyyy/MM/dd hh:MM:ss');
        return null;
    }
    return result.Format2String(fmt);
};

String.prototype.getMonthDay = function () {
    let curDate = new Date(this);
    let curMonth = curDate.getMonth();
    curDate.setMonth(curMonth + 1);
    curDate.setDate(0);
    return curDate.getDate();
};






