<template>
    <div class="body-box">
        <div class="tab-box">
            <div class="tap-wrap" :class="{selected:tabSelected==0}" @click="tabBtn(0)">
                <div class="tap-btn">{{nnapprovedNum}}</div>
            </div>
            <i></i>
            <div class="tap-wrap" :class="{selected:tabSelected==1}" @click="tabBtn(1)">
              <div class="tap-btn"></div>
            </div>
        </div>
        <div class="approval">
            <div class="batch-approval">
                <span class="batch-approval-text"></span><mt-switch v-model="batchSwitch"></mt-switch>
            </div>
            <ul class="approval-list bd-top-1">
                <li class="approval-list-cell">
                    <router-link to="approvalDetails">
                    <div class="approval-name"></div>
                    <div class="approval-info bd-bottom-1">
                        <p class="approval-title-date">
                            <span class="approval-title"></span><span class="approval-date">2017-11-12</span>
                        </p>
                        <p class="approval-start-date">2017-11-10 10:00</p>
                        <p class="approval-end-date">2017-11-13 17:00</p>
                        <p class="approval-state"></p>
                    </div>
                    </router-link>
                </li>
                <li class="approval-list-cell">
                    <router-link to="approvalDetails">
                    <div class="approval-name"></div>
                    <div class="approval-info">
                        <p class="approval-title-date">
                            <span class="approval-title"></span><span class="approval-date">2017-11-12</span>
                        </p>
                        <p class="approval-start-date">2017-11-10 10:00</p>
                        <p class="approval-end-date">2017-11-13 17:00</p>
                        <p class="approval-state"></p>
                    </div>
                    </router-link>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    name: 'approval',
    data(){
        return {
            nnapprovedNum:5,
            tabSelected:'',
            batchSwitch:false
        }
    },
    created(){
        console.log(this.$route.query.id);
    },
    methods:{
        tabBtn:function(index) {
            this.tabSelected = index;
        }
    }
}
</script>



